<template>
  <div class="exam-check">
    <div class="check-content">
      <!-- 考试信息 -->
      <div class="section-title">考试信息</div>
      <div class="info-table">
        <table>
          <tbody>
            <tr>
              <td class="label">考试名称</td>
              <td class="value">{{ examInfo.title }}</td>
            </tr>
            <tr>
              <td class="label">考试总分</td>
              <td class="value">{{ examInfo.totalScore }}</td>
            </tr>
            <tr>
              <td class="label">及格分数</td>
              <td class="value">{{ examInfo.passScore }}分</td>
            </tr>
            <tr>
              <td class="label">考试时长</td>
              <td class="value">{{ examInfo.duration }}分钟</td>
            </tr>
            <tr>
              <td class="label">考试类型</td>
              <td class="value">{{ examInfo.examType || '常规考试' }}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- 考试规则 -->
      <div class="exam-rules">
        <div class="rules-list">
          <!-- 如果有 content，显示 content 内容，否则显示默认规则 -->
          <template v-if="examInfo.content">
            <div v-html="examInfo.content"></div>
          </template>
          <template v-else>
            <p>1、多次考试取最后一次作为最终成绩；</p>
            <p>2、如有积分和证书将在考试结束后统一发放；</p>
          </template>
        </div>
      </div>

      <!-- 操作按钮 -->
      <div class="action-buttons">
        <el-button plain @click="$router.back()">返回</el-button>
        <el-button type="primary" @click="startExam">
          <i class="el-icon-position"></i>
          开始考试
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { getExamDetail } from '@/api/exam'

export default {
  name: 'ExamCheck',
  data() {
    return {
      examInfo: {
        title: '',
        duration: 0,
        totalScore: 0,
        passScore: 0,
        examType: '常规考试',
        content: ''
      }
    }
  },
  created() {
    // 优先使用路由传递的数据
    const query = this.$route.query
    if (query.title) {
      this.examInfo = {
        title: query.title,
        duration: parseInt(query.duration) || 0,
        totalScore: parseInt(query.totalScore) || 0,
        passScore: parseInt(query.passScore) || 0,
        examType: query.examType || '常规考试',
        content: query.content || ''
      }
    } else {
      // 如果没有传递数据，则请求接口获取
      this.getExamInfo()
    }
  },
  methods: {
    async getExamInfo() {
      try {
        const res = await getExamDetail(this.$route.params.id)
        if (res.code === 200) {
          this.examInfo = {
            title: res.data.title || res.data.questionPaperVo?.paperName,
            duration: res.data.totalTime,
            totalScore: res.data.questionPaperVo?.totalScore || 0,
            passScore: res.data.qualifyScore,
            examType: '常规考试',
            content: res.data.content || ''
          }
        }
      } catch (error) {
        console.error('获取考试信息失败:', error)
        this.$message.error('获取考试信息失败')
      }
    },
    startExam() {
      // 跳转到考试页面时也传递考试信息
      this.$router.push({
        path: `/exam/paper/${this.$route.params.id}`,
        query: {
          title: this.examInfo.title,
          duration: this.examInfo.duration,
          totalScore: this.examInfo.totalScore,
          passScore: this.examInfo.passScore
        }
      })
    }
  }
}
</script>

<style scoped>
.exam-check {
  padding: 20px;
  min-height: calc(100vh - 60px);
  background: #f5f7fa;
  display: flex;
  justify-content: center;
}

.check-content {
  width: 800px;
  background: #fff;
  padding: 30px;
  border-radius: 4px;
}

.section-title {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
  margin-bottom: 20px;
}

.info-table {
  margin-bottom: 30px;
}

.info-table table {
  width: 100%;
  border-collapse: collapse;
}

.info-table td {
  padding: 12px;
  border: 1px solid #EBEEF5;
}

.info-table .label {
  width: 120px;
  background-color: #F5F7FA;
  color: #606266;
}

.info-table .value {
  color: #303133;
}

.exam-rules {
  padding: 15px;
  border: 1px dashed #ff4d4f;
  border-radius: 4px;
  margin-bottom: 30px;
}

.rules-list {
  color: #606266;
  line-height: 1.8;
}

.rules-list :deep(p) {
  margin: 5px 0;
}

.rules-list :deep(img) {
  max-width: 100%;
  height: auto;
}

.rules-list :deep(table) {
  width: 100%;
  border-collapse: collapse;
  margin: 10px 0;
}

.rules-list :deep(td),
.rules-list :deep(th) {
  border: 1px solid #EBEEF5;
  padding: 8px;
}

.action-buttons {
  text-align: center;
  padding-top: 20px;
}

.action-buttons .el-button {
  width: 120px;
  margin: 0 10px;
}

.action-buttons .el-button i {
  margin-right: 5px;
}
</style> 