//import request from './request'
import request from '@/utils/request'
// 获取考试列表
export function getExamList(params) {
  return request({
    url: '/exam/questionExam/list',
    method: 'get',
    params
  })
}

// 获取考试详情
export function getExamDetail(id) {
  return request({
    url: `/exam/detail/${id}`,
    method: 'get'
  })
}

// 获取试卷试题列表
export function getExamPaper(id) {
  return request({
    // url: `/exam/papgerTopic/${id}`,
    url: `/exam/paper/${id}`,
    method: 'get'
  })
}

// 开始考试
export function startExam(data) {
  return request({
    url: '/exam/examScore/startExam',
    method: 'post',
    data
  })
}

// 提交试卷
export function submitPaper(data) {
  return request({
    url: '/exam/examSubmission/submit',
    method: 'post',
    data
  })
}

// 获取考试成绩详情
export function getExamScoreDetail(id) {
  return request({
    url: `/exam/examScore/${id}`,
    method: 'get'
  })
}

// 结束电脑使用
export function endComputerInfo(data) {
  return request({
    url: '/exam/computer/endComputerInfo',
    method: 'post',
    data: data
  })
}

// 新增考试电脑管理
export function addComputer(data) {
  return request({
    url: '/exam/computer',
    method: 'post',
    data: data
  })
}